import { useEffect, useState } from 'react'

export const useScrollToTop = () => {
   // Dclaration de 'isVisible' initialis  'false'
  const [isVisible, setIsVisible] = useState(false)

  // Fonction permettant de basculer la visibilit de l'lment en fonction de la position de la fentre
  const toggleVisibility = () => {
    if (window.pageYOffset > 300) {
      setIsVisible(true)
    } else {
      setIsVisible(false)
    }
  }

  // Fonction pour faire dfiler la page vers le haut
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    })
  }

  // Ajout d'un couteur d'vnement 'scroll'  la fentre
  useEffect(() => {
    window.addEventListener('scroll', toggleVisibility)
    return () => {
      window.removeEventListener('scroll', toggleVisibility)
    }
  }, [])

  return [isVisible, scrollToTop]
}

// Fonction pour faire dfiler la page vers le haut rapidement
export const scrollToTopSpeed = () => {
  window.scrollTo({
    top: 0
  })
}