import { useEffect } from 'react'
import Block from '../../components/Block/Block'
import { useScrollToTop } from '../../utils/functions/scrollToTop'
import styles from './About.module.css'

function About() {
  // Mettre  jour le titre de la page
  useEffect(() => {
    document.title = 'Claire De Vito -  propos'
  }, [])

  // Dfinir un tat pour afficher ou masquer le bouton 'retour en haut de page'
  const [isVisible, scrollToTop] = useScrollToTop()

  return (
    <main role='main'>
      <div className={styles.blockFlex}>
        <section aria-label='Prsentation complte' className={styles.block1}>
          <Block>
            <h2 className='visually-hidden'>Prsentation</h2>
            <p>J'ai, aujourd'hui, un peu plus de la trentaine, et ai vcu toute mon enfance partage entre l'Alsace et la Bretagne. Je vis maintenant en rgion parisienne, dans le Val d'Oise.</p>
            <p>Aprs avoir obtenu mon <strong>baccalaurat scientifique</strong>, je me suis dirige vers les <strong>Mathmatiques</strong>, ce qui me semblait logique et naturel.</p>
            <p>Une fois un bac +2 dans cette matire valid, j'ai ralis que j'avais besoin d'espace. tant sportive et aimant la nature, j'ai alors totalement chang de voie, et ai choisi de travailler avec les chiens.</p>
            <p>Fin 2021, j'ai ressenti l'envie de renouer avec mon premier amour, l'<strong>informatique</strong>. Quelques recherches sur internet m'ont amene  m'intresser aux langages HTML et CSS, puis au JavaScript.</p>
            <p>Une fois prise de cette discipline, il m'tait impossible de m'arrter l. J'ai alors pris la dcision de dbuter une <strong>formation</strong> diplmante, afin d'en apprendre plus sur le mtier de <strong>dveloppeur web</strong>.</p>
            <p>Aujourd'hui, je suis en fin de formation chez OpenClassrooms. Mon objectif est d'allier mon emploi actuel, qui me satisfait pleinement, et ma passion pour le code, en <strong>freelance</strong>.</p>
          </Block>
        </section>
        <div className={styles.block2}>
          <img className={styles.codePicture} src={'/images/code.webp'} alt='Lignes de code' title='Lignes de code' />
        </div>
        <section aria-label='Ma formation' className={styles.block3}>
          <Block>
            <h2 className='visually-hidden'>Ma formation</h2>
            <p>J'ai ralis plusieurs projets avec OpenClassrooms, accessibles via la page d'Accueil (avec leurs liens GitHub respectifs). J'ai galement t reue en soutenance par des valuateurs de l'organisme pour chacun d'entre eux.</p>
            <p>En plus des <strong>comptences</strong> acquises (mentionnes sur la page principale), j'ai aussi appris  devenir <strong>autonome</strong>, et  chercher les rponses  mes questions. J'ai ralis que j'tais d'un caractre  la fois patient et tenace, face aux diffrents 'bugs' que j'ai pu rencontrer, parfois rsolus aprs de nombreuses heures de travail.</p>
            <p>Je suis ravie de m'tre lance dans cette aventure, car j'ai pris beaucoup de <strong>plaisir</strong>  participer  cette formation. Je souhaite poursuivre dans cette direction.</p>
          </Block>
        </section>
      </div>
      {isVisible && (
        <span className='iconeBackContainer'>
          <button onClick={scrollToTop} className='iconeBack'>
            <i className='fa-solid fa-circle-chevron-up fa-4x'></i>
            <span className='visually-hidden'>Retour en haut de page</span>
          </button>
        </span>
      )}
    </main>
  )
}

export default About
